<template>
  <div>
    <div class="filter-container">
      <el-row :gutter="10">
        <el-col :sm="24" :md="12">
          <el-button class="filter-item" @click="handleCreate" type="primary" icon="el-icon-plus">{{$t('table.add')}}</el-button>
          <el-button class="filter-item" type="danger" :disabled="multipleSelection.length <= 0"
            v-if="multipleSelection.length>0"
            @click="handleDelete(multipleSelection)"
            icon="el-icon-delete">{{$t('table.delete')}} ({{multipleSelection.length}} item)</el-button>
        </el-col>
        <el-col :sm="24" :md="12">
          <el-input placeholder="Search" v-model="urlParams.s" class="input-with-select" @keyup.enter.native="getProducts">
            <el-button slot="append" icon="el-icon-search" @click="getProducts"></el-button>
          </el-input>
        </el-col>
      </el-row>
    </div>

    <div class="table-container">
      <el-table :data="products" v-loading="loading" element-loading-text="Memuat data..." @selection-change="handleSelectionChange" border fit highlight-current-row style="width: 100%">
        <el-table-column type="selection" width="55" style="margin:0 auto;"></el-table-column>
        <el-table-column prop="name" label="Name" sortable show-overflow-tooltip>
          <template slot-scope="scope">
            <router-link :to="{ name: 'productDetail', params: { id: scope.row.product_id }}">
              <a>{{scope.row.name}}</a>
            </router-link>
          </template>
        </el-table-column>
        <el-table-column label="Categories">
          <template slot-scope="scope">
            <el-tag
              style="margin:5px"
              v-for="category in scope.row.categories" :key="category.category_id"
              type="info">{{category.name}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="Suppliers">
          <template slot-scope="scope">
              <el-tag
                style="margin:5px"
                v-for="supplier in scope.row.suppliers" :key="supplier.supplier_id"
                type="success">{{supplier.name}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="Stock">
          <template slot-scope="scope">
            <div v-for="shop in scope.row.shops" :key="shop.shop_id">
              <p>{{shop.shop_name}}: {{shop.stock}}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="stock_price" label="Stock Price" sortable show-overflow-tooltip>
          <template slot-scope="scope">
            {{scope.row.stock_price | toIdr}}
          </template>
        </el-table-column>
        <el-table-column label="Prices">
          <template slot-scope="scope">
            <div v-for="(price, index) in scope.row.prices" :key="index">
              <p>{{price.type}}: {{price.price | toIdr}}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column align="center" :label="$t('table.actions')" width="230" class-name="small-padding fixed-width">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="handleUpdate(scope.row)" icon="el-icon-edit">{{$t('table.edit')}}</el-button>
            <el-button type="danger" size="mini" style="width:80px;" @click="handleDelete([scope.row])" icon="el-icon-delete">{{$t('table.delete')}}</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- Pagination -->
      <div class="pagination">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pagination.currentPage"
          :page-sizes="pagination.sizes"
          :page-size="pagination.pageSize"
          layout="sizes, total, prev, pager, next"
          :total="pagination.total">
        </el-pagination>
      </div>
    </div>

    <el-dialog :title="textMap[formDialog.status]" :visible.sync="formDialog.visible" :width="formDialog.dialog.width" :before-close="handleCloseDialog">
      <el-form :model="productForm" :rules="productFormRules" ref="productForm" :label-position="formDialog.dialog.labelPosition" label-width="200px">

        <el-form-item label="Name" prop="name">
          <el-input type="text" v-model="productForm.name" placeholder="name"></el-input>
        </el-form-item>

        <!-- <el-form-item label="Suppliers">
          <el-select v-model="productForm.suppliers" multiple placeholder="Select supplier" style="width:100%">
            <el-option
              v-for="item in suppliers"
              :key="item.supplier_id"
              :label="item.name"
              :value="item.supplier_id">
            </el-option>
          </el-select>
        </el-form-item> -->

        <el-form-item label="Category" prop="category_id">
          <el-select v-model="productForm.category_ids" multiple placeholder="Select" style="width:100%">
            <el-option
              v-for="item in productCategories"
              :key="item.category_id"
              :label="item.name"
              :value="item.category_id">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="Stock Price" prop="stock_price">
          <el-input type="number" v-model.number="productForm.stock_price" placeholder="stock price"></el-input>
        </el-form-item>

        <div class="sub-title">
          <h3>Supplier</h3>
        </div>

        <div style="max-height:500px;overflow-y:auto;">
          <el-form-item
            v-for="(supplier, index) in productForm.suppliers"
            :label="'Supplier ' + (index+1)"
            :key="'supplier-'+index"
            :prop="'suppliers.' + index + '.supplier'"
            >
            <el-row :gutter="5">
              <el-col :sm="8">
                <el-select
                  style="width:100%"
                  v-model="supplier.supplier_id"
                  filterable
                  remote
                  placeholder="Search supplier"
                  :remote-method="getProducts"
                  :loading="loading">
                  <el-option
                    v-for="item in suppliers"
                    :key="item.supplier_id"
                    :label="item.name"
                    :value="item.supplier_id">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :sm="6">
                <el-input type="number" v-model.number="supplier.buying_price" min="0" placeholder="buying price"></el-input>
              </el-col>
              <el-col :sm="2">
                <el-button type="danger" icon="el-icon-delete" @click.prevent="removeSupplier(supplier)"></el-button>
              </el-col>
            </el-row>
          </el-form-item>
        </div>

        <el-form-item>
          <el-button style="margin-left:10px" size="mini" type="primary" icon="el-icon-plus" @click="addSupplier">Add Supplier</el-button>
        </el-form-item>

        <div class="sub-title">
          <h3>Stock</h3>
        </div>

        <el-form-item
          v-for="(shop, index) in productForm.shops"
          :label="shop.shop_name"
          :key="'shop-'+index"
          :prop="'shops.' + index + '.shop'"
          :rules="productFormRules.shops">
          <el-row :gutter="5">
            <el-col :sm="8">
              <el-input type="number" v-model.number="shop.stock" min="0" placeholder="stock"></el-input>
            </el-col>
            <el-col :sm="2">
              <el-button type="danger" icon="el-icon-delete" @click.prevent="removePrice(shop)"></el-button>
            </el-col>
          </el-row>
        </el-form-item>

        <!-- <el-form-item>
          <el-button style="margin-left:10px" size="mini" type="primary" icon="el-icon-plus" @click="addProductShop">Add Shop</el-button>
        </el-form-item> -->


        <div class="sub-title">
          <h3>Selling Price</h3>
        </div>

        <el-form-item
          v-for="(price, index) in productForm.prices"
          :label="'Price ' + (index+1)"
          :key="'price-'+index"
          :prop="'prices.' + index + '.price'"
          :rules="productFormRules.prices">
          <el-row :gutter="5">
            <el-col :sm="8">
              <el-input type="text" v-model="price.type" placeholder="Type"></el-input>
            </el-col>
            <el-col :sm="8">
              <el-input type="number" v-model.number="price.price" min="0" placeholder="price value"></el-input>
            </el-col>
            <el-col :sm="2">
              <el-button type="danger" icon="el-icon-delete" @click.prevent="removePrice(price)"></el-button>
            </el-col>
          </el-row>
        </el-form-item>

        <el-form-item>
          <el-button style="margin-left:10px" size="mini" type="primary" icon="el-icon-plus" @click="addPrice">Add Price</el-button>
        </el-form-item>

        <el-form-item>
          <el-button @click="handleCloseDialog">{{$t('table.cancel')}}</el-button>
          <el-button v-if="formDialog.status==='create'" :loading="loading" type="primary" @click="createData">{{$t('table.confirm')}}</el-button>
          <el-button v-else type="primary" @click="updateData">{{$t('table.confirm')}}</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title="Delete Confirmation"
      :visible.sync="deleteDialog.visible"
      :width="formDialog.dialog.width"
      center>
      <span>Are you sure want to delete?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog.visible = false">Cancel</el-button>
        <el-button type="primary" @click="deleteData">Yes</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import { dialog } from "@/responsive";
import { arrayToObject } from "@/utils/arrayToObject";

const formFields = [
  "name",
  ["category_ids", []],
  ["prices", [{
    price: null,
    type: null,
  }]],
  ["shops", []],
  ["suppliers", [{
    supplier_id: null,
    buying_price: null,
  }]],
];

export default {
  data() {
    return {
      products: [],
      shops: [],
      productCategories: [],
      suppliers: [],
      // roles: [],
      loading: false,
      textMap: {
        update: "Edit",
        create: "Create"
      },

      // form
      formFields,
      productForm: arrayToObject(formFields),
      productFormRules: {
        name: [{ required: true, max: 255 }],
        category_ids: [{ type: "array", required: true, trigger: "change" }],
        stock_price: [{ required: true, type: "number" }],
        prices: [
          {
            required: true,
            message: "Price can not be null",
            trigger: "blur"
          }
        ]
      },

      // edit
      formDialog: {
        visible: false,
        status: "",
        selectedId: undefined,
        dialog: dialog.md
      },

      // delete
      deleteDialog: {
        visible: false,
        dialog: dialog.md30
      },
      deleteProductIds: [],

      // table
      multipleSelection: [],

      // Pagination
      pagination: {
        total: 0,
        pageSize: 10,
        currentPage: 1,
        sizes: [5, 10, 20, 30, 40, 50, 100]
      },

      // Get
      urlParams: {
        s: "",
        l: 10,
        p: 1,
        oby: "created_at", // order_by (product_id|name|description|stock|created_at)
        sort: "desc"
      },

      // Responsive
      windowWidth: window.innerWidth
    };
  },

  methods: {
    // Create
    handleCreate() {
      this.resetForm();

      const shops = this.shops.map(shop => {
        return {
          shop_id: shop.shop_id,
          shop_name: shop.name,
          stock: 0
        };
      });
      this.productForm = {
        ...this.productForm,
        shops: shops
      };
      console.log("===>",this.productForm);

      this.formDialog = {
        ...this.formDialog,
        status: "create",
        visible: true
      };
      this.$nextTick(() => {
        this.$refs["productForm"].clearValidate();
      });

      // Responsive
      this.makeDialogResponsive();
    },
    createData() {
      this.$refs.productForm.validate(valid => {
        if (valid) {
          this.loading = true;

          this.$store
            .dispatch("AddProduct", this.productForm)
            .then(res => {
              this.getProducts();

              this.$message.success(res.data.messages[0]);
              this.formDialog.visible = false;
              this.loading = false;
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              errorMsg.forEach(msg => {
                this.$message.error(msg);
              });
              this.loading = false;
            });
        }
      });
    },

    // Update
    handleUpdate(row) {
      const shops = this.shops.map(shop => {
        const selectedProductShops = row.shops.filter(selectedShop => {
          return selectedShop.shop_id === shop.shop_id;
        })[0];

        let stock = 0;
        if (selectedProductShops) {
          stock = selectedProductShops.stock;
        }

        return {
          shop_id: shop.shop_id,
          shop_name: shop.name,
          stock: stock
        };
      });
      this.productForm = {
        name: row.name,
        category_ids: row.categories.map(category => {
          return category.category_id;
        }),
        stock_price: row.stock_price,
        prices: row.prices,
        shops: shops,
        suppliers: row.suppliers,
      };
      this.formDialog = {
        status: "update",
        visible: true,
        selectedId: row.product_id
      };
      this.productFormRules.password = [{ required: false }];
      this.$nextTick(() => {
        this.$refs["productForm"].clearValidate();
      });

      // Responsive
      this.makeDialogResponsive();
    },
    updateData() {
      this.$refs.productForm.validate(valid => {
        if (valid) {
          this.loading = true;

          // add product Id
          this.productForm.productId = this.formDialog.selectedId;

          this.$store
            .dispatch("UpdateProduct", this.productForm)
            .then(res => {
              this.getProducts();

              this.loading = false;
              this.formDialog.visible = false;
              this.$message.success(res.data.messages[0]);
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              errorMsg.forEach(msg => {
                this.$message.error(msg);
              });
              this.loading = false;
            });
        }
      });
    },

    // Delete
    handleDelete(products) {
      this.deleteDialog.visible = true;
      this.deleteProductIds = products.map(product => {
        return product.product_id;
      });

      // Responsive
      this.makeDialogResponsive(dialog.sm, dialog.md30);
    },
    deleteData() {
      this.deleteDialog.visible = false;
      this.$store
        .dispatch("DeleteProduct", this.deleteProductIds)
        .then(res => {
          this.getProducts();

          this.$message.success(res.data.messages[0]);
          this.deleteDialog.visible = false;
          this.deleteProductIds = [];
        })
        .catch(error => {
          const errorMsg = error.response.data.messages;
          errorMsg.forEach(msg => {
            this.$message.error(msg);
          });
        });
    },

    // Form
    resetForm() {
      this.productForm = arrayToObject(this.formFields);
    },
    removePrice(item) {
      var index = this.productForm.prices.indexOf(item);
      if (index !== -1) {
        this.productForm.prices.splice(index, 1);
      }
    },
    addPrice() {
      this.productForm.prices.push({
        price: 0,
        type: ""
      });
    },
    addSupplier() {
      this.productForm.suppliers.push({
        supplier_id: null,
        buying_price: null
      });
    },
    removeSupplier(item) {
      var index = this.productForm.suppliers.indexOf(item);
      if (index !== -1) {
        this.productForm.suppliers.splice(index, 1);
      }
    },

    // Dialog
    handleCloseDialog(done) {
      this.formDialog.visible = false;
      this.getProducts();
    },

    // Table
    handleSelectionChange(products) {
      this.multipleSelection = products;
    },

    // Pagination
    handleSizeChange(pageSize) {
      this.pagination = {
        ...this.pagination,
        pageSize
      };
      this.urlParams = {
        ...this.urlParams,
        l: pageSize
      };

      this.getProducts();
    },
    handleCurrentChange(currentPage) {
      this.pagination = {
        ...this.pagination,
        currentPage
      };
      this.urlParams = {
        ...this.urlParams,
        p: currentPage
      };
      this.getProducts();
    },

    // Get
    getProducts() {
      this.loading = true;
      this.$store.dispatch("FetchProducts", this.urlParams).then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        this.products = data.data.products;
        this.loading = false;

        // set pagination
        this.pagination = {
          ...this.pagination,
          total: data.data.total
        };
      });
    },
    getShops() {
      this.loading = true;
      this.$store.dispatch("FetchShops").then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        this.shops = data.data.shops;
        this.loading = false;
      });
    },
    getProductCategories() {
      this.loading = true;
      this.$store.dispatch("FetchProductCategories").then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        this.productCategories = data.data.categories;
        this.loading = false;
      });
    },
    getSuppliers() {
      this.loading = true;
      this.$store.dispatch("FetchSuppliers").then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        this.suppliers = data.data.suppliers;
        this.loading = false;
      });
    },

    // Responsive
    makeDialogResponsive(sm = dialog.sm, md = dialog.md) {
      if (this.windowWidth < 990) {
        this.formDialog = {
          ...this.formDialog,
          dialog: sm
        };
      } else {
        this.formDialog = {
          ...this.formDialog,
          dialog: md
        };
      }
    }
  },

  watch: {
    windowWidth(newWidth, oldWidth) {
      this.makeDialogResponsive();
    }
  },

  created() {
    this.getProducts();
    this.getShops();
    this.getProductCategories();
    this.getSuppliers();

    // this.$store.dispatch("FetchRoles").then(roles => {
    //   this.roles = roles;
    // });
  },

  mounted() {
    let that = this;
    this.$nextTick(function() {
      window.addEventListener("resize", function(e) {
        that.windowWidth = window.innerWidth;
      });
    });
  }
};
</script>

<style scoped>
a:hover {
  color: #5867dd;
}
</style>



